import { useState } from "react";
import "./styles.css";

export default function App() {
  const [phoneNumber, setPhoneNumber] = useState(null);
  return (
    <div className="App">
      <h1>Chat in whatsapp without saving contact</h1>
      <h6>Please enter a phone number with country code</h6>
      <input
        placeholder="Please enter Phone Number"
        type="number"
        onBlur={(e) => {
          const formattedPhoneNum = e.target.value.replace(/[^0-9a-z]/gi, "");
          setPhoneNumber(formattedPhoneNum);
        }}
      />
      <br />
      <button
        onClick={() => window.open(`https://wa.me/${phoneNumber}`, "_blank")}
      >
        chat
      </button>
    </div>
  );
}
